/* Fonts */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800;900&display=swap');


/* Global CSS */
:root {
    --main-color: #7875fe;
    --color-1: #e91e63;
    --color-2: #f5ae10;
    --color-3: #09d69c;
    --bg-dark: #2b2c2f;
    --main-to-dark-color: var(--main-color);
    --dark-to-main-color: var(--bg-dark);
    --shadow-black-100: 0 5px 15px rgba(0, 0, 0, 0.1);
    --shadow-black-300: 0 5px 15px rgba(0, 0, 0, 0.3);
    --black-900: #000000;
    --black-400: #555555;
    --black-100: #f7f7f7;
    --black-000: #fff;
    --black-alpha-100: rgba(0, 0, 0, 0.5);
}

body {
    font-family: 'Poppins', sans-serif;
    font-size: 16px;
    font-weight: 400;
    overflow-x: hidden;
    line-height: 1.5;
    background-color: #fff;
}

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    outline: none !important;
}
a:hover,
 a {
    text-decoration: none !important;
}

img {
    max-width: 100%;
    vertical-align: middle;
}

ul {
    list-style: none;
}

.btn-1 {
    background-color: #fff;
    padding: 12px 30px;
    border: none;
    border-radius: 30px;
    color: var(--main-color);
    font-size: 16px;
    text-transform: capitalize;
    transition: all 0.5s ease;
    box-shadow: var(--shadow-black-300);
    font-weight: 500;
}

.btn-2 {
    background-color: var(--main-color);
    padding: 12px 30px;
    border: none;
    border-radius: 30px;
    color: #fff;
    font-size: 16px;
    text-transform: capitalize;
    transition: all 0.5s ease;
    box-shadow: var(--shadow-black-100);
    font-weight: 500;
}

.btn-2:focus {
    box-shadow: var(--shadow-black-100);
}

.btn-1:focus {
    box-shadow: var(--shadow-black-300);
}

.btn-1:hover {
    color: #ffffff;
    background-color: var(--main-color);
}

.btn-2:hover {
    color: var(--main-color);
    background-color: #ffffff;
}

@keyframes spin_01{
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
}
@keyframes bounceTop_01{
    0%,100%{
        transform: translateY(-30px)
    }
    50%{
        transform: translateY(0px);
    }
}
@keyframes pulse_01{
    0%{
        transform: scale(0.94);
        box-shadow: 0 0 0 0 rgba(255,255,255,0.7);
    }
    70%{
        transform: scale(1);
        box-shadow: 0 0 0 12px rgba(255,255,255,0);
    }
    100%{
        transform: scale(0.94);
        box-shadow: 0 0 0 0 rgba(255,255,255,0);
    }
}

@keyframes zoomInOut_01{
    0%{
        transform: scale(0);
    }
    100%{
        transform: scale(1);
        opacity: 0;
    }
}

.effect-wrap .effect {
    position: absolute;
    z-index: -1;
}

.effect-wrap .effect-1 {
    left: 20%;
    top: 20%;
    color: var(--color-2);
    animation: spin_01 5s linear infinite;
}

.effect-wrap .effect-2 {
    right: 5%;
    top: 10%;
    color: rgba(255, 255, 255, 0.5);
    animation: spin_01 7s linear infinite;
}

.effect-wrap .effect-3 {
    left: 5%;
    bottom: 30%;
    color: var(--color-3);
    animation: bounceTop_01 3s linear infinite;
}

.section-padding {
    padding: 80px 0;
}

.section-title {
    margin-bottom: 60px;
}

.section-padding h2 {
    font-size: 40px;
    color: var(--black-900);
    font-weight: 700;
    text-transform: capitalize;
    text-align: center;
    margin: 0;
}

.section-padding h2 span {
    color: var(--main-color);
}

.owl-carousel .owl-dots {
    padding: 0 15px;
    text-align: center;
    margin-top: 20px;
}

.owl-carousel button.owl-dot {
    height: 6px;
    width: 24px;
    background-color: #ddd !important;
    display: inline-block;
    margin: 0 4px;
    border-radius: 4px;
}
.owl-carousel button.owl-dot.active {
    background-color: var(--main-color) !important;
}

/* -------------------
Preloader
-------------------- */
.preloader{
    position: fixed;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    z-index: 2100;
    background-color: var(--main-to-dark-color);
    display: flex;
    align-items: center;
    justify-content: center;
}
.preloader span{
    display: block;
    height: 60px;
    width: 60px;
    background-color: var(--dark-to-main-color);
    border-radius: 50%;
    animation: zoomInOut_01 1s ease infinite;
}

/* ./Preloader */
/* Navbar CSS */

.navbar {
    background-color: var(--main-to-dark-color);
    padding: 20px 0;
    transition: all 0.3s ease;
}
.navbar.navbar_shrink {
    box-shadow: 0 10px 10px rgba(0, 0, 0, 0.1);
    background-color: var(--main-color);
    padding: 10px 0;
    position: relative;
}

.navbar>.container {
    padding: 15px 0;
}

.navbar .navbar-brand a{
    font-size: 30px;
    color: #fff;
    font-weight: 500;
    text-transform: capitalize;
}

.navbar .nav-item {
    margin-left: 40px;
}

.navbar .nav_link {
    color: #fff;
    text-transform: capitalize;
    font-weight: 400;
    padding: 5px 0;
    position: relative;
    font-size: 16px;
}

.navbar .nav-item .nav_link::before {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    right: 0;
    height: 1px;
    transform: scale(0);
    background-color: #fff;
    transition: all 0.5s ease;
}

.navbar .nav-item .nav_link.active::before,
.navbar .nav-item .nav_link:hover::before {
    transform: scale(1);
}

/* ./Navbar CSS */
/* Home Section CSS */
.home {
    min-height: 100vh;
    padding: 150px 0;
    background-color: var(--main-to-dark-color);
    border-radius: 0 0 200px 0;
    position: relative;
    z-index: 1;
}

.home .home-text h1 {
    font-size: 55px;
    color: #fff;
    font-weight: 700;
    margin: 0 0 20px
}

.home .home-text p {
    font-size: 18px;
    line-height: 28px;
    color: #fff;
    font-weight: 300;
}

.home .home-btn {
    margin-top: 40px;
}

.home .home-btn .video-play-btn{
    margin-left: 30px;
    height: 50px;
    width: 50px;
    padding: 0;
    font-size: 16px;
    animation: pulse_01 2s ease infinite;
}
.home .home-btn .video-play-btn i:hover{
    animation: none;
    box-shadow: var(--shadow-black-300);
}
.home .home-btn .video-play-btn i{
    line-height: 50px;
}


.home .home-img img {
    max-width: 250px;
    width: 100%;
    box-shadow: vr(--shadow-black-100);
    border-radius: 32px;
    animation: bounceTop_01 3s ease infinite;
}

.home-img {
    position: relative;
}

.home-img .circle {
    position: absolute;
    z-index: 1;
    height: 400px;
    width: 400px;
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-radius: 50%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.home-img .circle:before {
    content: '';
    position: absolute;
    height: 60px;
    width: 60px;
    background-color: rgba(255, 255, 255, 0.4);
    border-radius: 50%;
    left: 30px;
    top: 30px;
    transform-origin: 170px 170px;
    animation: spin_01 10s linear infinite;
}

/* ./Home Section

/* video-popup */
.video-popup {
    padding: 30px;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 1999;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    visibility: hidden;
    transition: all 0.5s ease;
}

.video-popup.open {
    opacity: 1;
    visibility: visible;
}

.video-popup-inner {
    width: 100%;
    max-width: 900px;
    position: relative;
}

.video-popup .video-popup-close {
    position: absolute;
    right: 0;
    top: -30px;
    height: 30px;
    width: 30px;
    font-size: 16px;
    text-align: center;
    cursor: pointer;
    color: #fff;
}

.video-popup .iframe-box {
    height: 0;
    overflow: hidden;
    padding-top: 56.25%;
}

.video-popup #player-1 {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    border: none;
}


/* ./video-popup */

/* features */
.features .feature-item {
    box-shadow: var(--shadow-black-100);
    text-align: center;
    margin: 15px;
    padding: 30px;
    border-radius: 10px;
    text-align: center;
    position: relative;
    z-index: 1;
    overflow: hidden;
}

.features .feature-item::before,
.features .feature-item::after {
    content: '';
    position: absolute;
    z-index: -1;
    border-radius: 50%;
    transition: al 0.3s ease;
    opacity: 0;
}

.features .feature-item:hover::before {
    left: 10px;
    opacity: 1;
}

.features .feature-item:hover::after {
    right: -15px;
    opacity: 1;
}

.features .feature-item::before {
    height: 20px;
    width: 20px;
    background-color: var(--color-1);
    left: -20px;
    top: 40%;
}

.features .feature-item::after {
    height: 30px;
    width: 30px;
    background-color: var(--color-2);
    right: -30px;
    top: 80%;
}

.features .feature-item .icon {
    height: 60px;
    width: 60px;
    margin: 0 auto 30px;
    font-size: 30px;
    color: var(--main-color);
    z-index: 1;
    position: relative;
    transition: all 0.5s ease;
}

.features .feature-item .icon::before {
    content: '';
    position: absolute;
    left: 100%;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: var(--main-color);
    transition: all 0.5s ease;
    z-index: -1;
    opacity: 0;
}

.features .feature-item:hover .icon {
    color: #fff;
}

.features .feature-item:hover .icon::before {
    left: 0;
    border-radius: 50%;
    opacity: 1;
}

.features .feature-item .icon i {
    line-height: 60px;
}

.features .feature-item h3 {
    font-size: 22px;
    margin: 0 0 20px;
    color: var(--black-999);
    font-weight: 500;
    text-transform: capitalize;
}

.features .feature-item p {
    font-size: 16px;
    line-height: 26px;
    font-weight: 300;
    color: var(--black-400);
    margin: 0 0 15px;
}

/* ./features */



/* Fun Facts Section */
.fun-facts {
    background-color: var(--black-100);
}

.fun-facts .section-title {
    margin-bottom: 20px;
}

.fun-facts .section-title h2 {
    text-align: left;
}

.fun-facts-img img {
    max-width: 250px;
    width: 100%;
    box-shadow: vr(--shadow-black-100);
    border-radius: 32px;
    animation: bounceTop_01 3s ease infinite;
}

.fun-facts-text p {
    font-size: 16px;
    line-height: 26px;
    color: var(--black-400);
    margin: 0 0 15px;
    font-weight: 300;
}

.fun-fact-item {
    padding: 40px 30px;
    text-align: center;
    box-shadow: var(--shadow-black-100);
    border-radius: 10px;
    margin-top: 30px;
}

.fun-fact-item h3 {
    font-weight: 700;
    color: #fff;
    font-size: 40px;
    margin: 0 0 5px;
}

.fun-fact-item span {
    font-size: 16px;
    text-transform: capitalize;
    font-weight: 300;
    margin: 0;
    line-height: 26px;
    color: #fff;
}
.fun-fact-item.style-1 {
    background-color: var(--color-1);
}

.fun-fact-item.style-2 {
    background-color: var(--color-2);
}

.fun-fact-item.style-3 {
    background-color: var(--color-3);
}

.fun-fact-item.style-4 {
    background-color: var(--main-color);
}

/* ./Fun Facts Section */

/* Screenshots Section */
.screenshots-item {
    margin: 15px;
}

.screenshots-item img {
    width: 100%;
    max-width: 250px;
    display: block;
    margin: auto;
}

/* ./Screenshots Section */

/* App Download Section */
.app-download {
    background-image: url('../src/components/images/download-app-bg.jpg');
    background-position: center top;
    background-attachment: fixed;
    background-repeat: no-repeat;
    position: relative;
    z-index: 1;
}

.app-download .section-title h2 {
    color: #fff;
}

.app-download::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: var(--main-to-dark-color);
    opacity: 0.8;
}

.app-download-item {
    background-color: var(--black-000);
    padding: 30px;
    text-align: center;
    border-radius: 30px;
    box-shadow: var(--shadow-black-100);
}

.app-download-item .icon {
    font-size: 30px;
    color: var(--main-color);
    display: inline-block;
}

.app-download-item h3 {
    font-size: 22px;
    color: var(--black-900);
    margin: 20px 0;
    font-weight: 500;
    text-transform: capitalize;
}

.app-download-item p {
    font-size: 16px;
    color: var(--black-400);
    font-weight: 300;
    margin: 0 0 30px;
    line-height: 26px;
}

/* ./App Download Section */

/* how-it-works Section */

.how-it-works-item {
    padding: 30px;
    text-align: center;
    position: relative;
}

.how-it-works-item.line-right::before {
    content: '';
    position: absolute;
    height: 2px;
    width: 100%;
    left: 50%;
    top: 60px;
    background-color: var(--main-color);
    z-index: -1;
}

.how-it-works-item .step {
    display: inline-block;
    height: 60px;
    width: 60px;
    background-color: var(--main-color);
    font-size: 30px;
    text-align: center;
    line-height: 60px;
    color: #fff;
    border-radius: 100%;
    font-weight: 500;
    margin: 0 0 15px;
}

.how-it-works-item h3 {
    margin: 20px 0;
    font-size: 22px;
    font-weight: 500;
    color: var(--black-900);
    text-transform: capitalize;
}

.how-it-works-item p {
    font-size: 16px;
    font-weight: 300;
    margin: 0;
    color: var(--black-400);
    line-height: 26px;
}

/* ./how-it-works Section */

/* Testimonials Section */
.testimonials {
    background-color: var(--black-100);
}

.testimonials-item {
    padding: 30px;
    margin: 15px;
    box-shadow: var(--shadow-black-100);
    border-radius: 10px;
    text-align: center;
    height: 100%;
}

.testimonials-item .img-box {
    height: 100px;
    width: 100px;
    display: inline-block;
    margin-bottom: 30px;
    border: 4px solid var(--main-color);
    border-radius: 50%;
    position: relative;
}

.testimonials-item .img-box img {
    border-radius: 50%;
    width: 100%;
}
.testimonials-item .img-box i{
    position: absolute;
    height: 30px;
    width: 30px;
    background-color: var(--main-color);
    border-radius: 50%;
    color: #fff;
    font-size: 12px;
    line-height: 30px;
    text-align: center;
    left: calc(100% - 15px);
    top: calc(50% - 15px);
}
.testimonials-item p {
    color: var(--black-400);
    line-height: 26px;
    font-weight: 300;
}

.testimonials-item h3 {
    font-size: 18px;
    color: var(--black-900);
    font-weight: 500;
    margin: 0 0 5px;
    text-transform: capitalize;
}

.testimonials-item span {
    display: block;
    font-size: 16px;
    color: var(--black-400);
    font-weight: 300;
}

.testimonials-item .rating {
    margin-top: 10px;
}

.testimonials-item .rating  {
    display: inline-block;
    font-size: 16px;
    color: var(--color-2);
}

/* ./Testimonials Section */

#line .line-right::before{display: none;}



/* Video Popup */

 .modal-video-movie-wrap {
    padding: 0 !important;
}

.modal-video iframe {
    width: 850px;
    height: 450px;
    z-index: 999;
    position: relative;
}

.modal-video {
    position: absolute;
    top: -80px;
    left: 0;
    content: '';
    right: 0;
    bottom: 0;
    transform: translate(114px, -50px);
    z-index: 99;
}


button.modal-video-close-btn {
    position: absolute;
    top: -30px;
    width: 20px;
    height: 20px;
    right: -180px;
    left: 0;
    content: '';
    display: block;
    margin-left: auto;
    border: 1px solid #ddd;
    background: transparent;
}

/* ./Video Popup */

/* ----------------
Pricing Section
------------------------- */
.pricing-plan{
    box-shadow: var(--shadow-black-100);
    border-radius: 10px;
}
.pricing-plan .pricing-header{
    padding: 20px 30px;
    border-bottom: 1px solid var(--black-alpha-100);
}
.pricing-plan .pricing-header h3{
    text-align: center;
    font-size: 30px;
    color: var(--black-900);
    font-weight: 500;
    text-transform: capitalize;
    margin: 0;
}
.pricing-plan .pricing-price{
    padding: 40px 30px;
    display: flex;
    justify-content: center;
    line-height: 0.7;
}
.pricing-plan .pricing-price .currency{
    font-size: 16px;
    font-weight: 300;
    color: var(--black-400);
    margin-right: 50px;
    align-self: flex-start;
}
.pricing-plan .pricing-price .price{
    font-size: 80px;
    font-weight: 700;
    color: var(--main-color);
}
.pricing-plan .pricing-price .period{
    font-size: 14px;
    font-weight: 300;
    color: var(--black-400);
    margin-right: 5px;
    align-self: flex-end;
    text-transform: uppercase;
}
.pricing-plan .pricing-body{
    padding: 0 30px;
}
.pricing-plan .pricing-body ul li{
    font-size: 16px;
    font-weight: 300;
    color: var(--black-400);
    padding: 10px 0 10px 30px;
    /* border-bottom: 1px solid var(--black-alpha-100); */
    line-height: 26px;
    position: relative;
}
.pricing-plan .pricing-body ul li:last-child{
    border-bottom: none;
}
.pricing-plan .pricing-body ul li i{
    color: var(--main-color);
    position: absolute;
    left: 0;
    top: 12px;
}
.pricing-plan .pricing-footer{
    padding: 20px 30px 30px;
    text-align: center;
}

/* -----------------------
Team Section
--------------------- */
.team{
    background-color: var(--black-100);
}
.team-item{
    margin: 15px;
    padding: 30px;
    border-radius: 10px;
    box-shadow: var(--shadow-black-100);
}
.team-item h3{
    font-size: 18px;
    color: var(--black-900);
    font-weight: 500;
    margin: 15px 0 5px;
    text-transform: capitalize;
}
.team-item span{
    display: block;
    font-size: 16px;
    font-weight: 300;
    color: var(--black-400);
    text-transform: capitalize;
}

/* --------------
Faq Section
---------------------- */
.faq .accordion-item:not(:last-child){
    margin-bottom: 20px;
}
.faq .accordion-header{
    box-shadow: var(--shadow-black-100);
    padding: 20px 50px 20px 30px;
    transition: all 0.5s ease;
    cursor: pointer;
    position: relative;
}
.faq .accordion-header::before{
    content: "\f067";
    font-family: 'Font Awesome 5 Free';
    font-weight: 900;
    position: absolute;
    height: 30px;
    width: 30px;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
    font-size: 16px;
    text-align: center;
    line-height: 30px;
    color: var(--black-400);
    transition: all 0.5s ease;
}
.faq .accordion-header:not(.collapsed)::before{
    content: "\f068";
    color: #ffffff;
}
.faq .accordion-header:not(.collapsed){
    background-color: var(--main-color);
    box-shadow: none;
}
.faq .accordion-header h3{
    font-size: 18px;
    font-weight: 500;
    color: var(--black-400);
    margin: 0;
    transition: all 0.5s ease;
}
.faq .accordion-header:not(.collapsed) h3{
    color: #ffffff;
}
.faq .accordion-body{
    padding: 20px 30px;
    background-color: var(--black-100);
}
.faq .accordion-body p{
    font-weight: 300;
    font-size: 16px;
    color: var(--black-400);
    line-height: 26px;
    margin: 0;
}

/* ---------------
Contact Section
---------------- */
.contact{
    background-color: var(--black-100);
}
.contact-info h3{
    font-size: 22px;
    color: var(--black-900);
    font-weight: 500;
    margin: 0 0 40px;
}
.contact-info-item {
    position: relative;
    padding-left: 55px;
    margin-bottom: 30px;
}
.contact-info-item i{
    position: absolute;
    height: 40px;
    width: 40px;
    left: 0;
    top: 0;
    border-radius: 50%;
    font-size: 16px;
    color: var(--main-color);
    border: 1px solid var(--main-color);
    text-align: center;
    line-height: 38px;
}
.contact-info-item h4{
    font-size: 18px;
    font-weight: 400;
    margin: 0 0 10px;
    color: var(--black-900);
}
.contact-info-item p{
    font-size: 16px;
    font-weight: 300;
    margin: 0;
    line-height: 26px;
    color: var(--black-400);
}

.contact-form .form-group{
    margin-bottom: 25px;
}
.contact-form .form-control{
    height: 52px;
    border: 1px solid transparent;
    box-shadow: var(--shadow-black-100);
    border-radius: 30px;
    padding: 0 24px;
    color: var(--black-900);
    background-color: var(--black-000);
    transition: all 0.5s ease;
}
.contact-form textarea.form-control{
    height: 140px;
    padding-top: 12px;
    resize: none;
}
/* textarea.form-control {
    line-height: 51px;    resize: none;

} */
.contact-form .form-control:focus{
    border-color: var(--main-color);
}

/* ------------------
Footer
------------------- */
.footer{
    background-color: var(--main-color);
    padding: 50px 0;
}
.footer-col h3{
    font-size: 20px;
    font-weight: 500;
    color: #ffffff;
    margin: 0 0 25px;
    text-transform: capitalize;
}
.footer-col p{
    font-size: 16px;
    color: rgba(255,255,255,0.9);
    font-weight: 300;
    line-height: 26px;
    margin: 0;
}
.footer-col ul{
    margin: 0;
}
.footer-col ul li:not(:last-child){
    margin-bottom: 10px;
}
.footer-col ul li a{
    font-size: 16px;
    color: rgba(255,255,255,0.9);
    font-weight: 300;
    display: block;
    text-transform: capitalize;
    transition: all 0.5s ease;
    text-decoration: none;
}
.footer-col ul li a:hover{
    padding-left: 5px;
}
.footer .copyright-text{
    padding: 20px 0;
    text-align: center;
    font-size: 16px;
    color: rgba(255,255,255,0.9);
    font-weight: 300;
    margin: 50px 0 0;
    border-top: 1px solid rgba(255,255,2555,0.2);
}

/* -------------------------------
Toggle Theme - light and dark mode
------------------------------ */
.toggle-theme{
    position: fixed;
    right: 0;
    top: calc(50% - 20px);
    height: 40px; 
    width: 40px;
    background-color: var(--dark-to-main-color);
    z-index: 1200;   
    color: #ffffff;
    cursor: pointer;
    border-radius: 50%;
    box-shadow: var(--shadow-black-300);
    font-size: 16px;
    text-align: center;
}
.toggle-theme i{
    line-height: 40px;
}

/* ----------------------
Responsive
----------------------------- */
@media(max-width: 991px){
    .navbar-toggler{
        background-color: var(--dark-to-main-color);
        box-shadow: var(--shadow-black-300);
        height: 34px;
        width: 44px;
        padding: 0;
        font-size: 17px;
        border-radius: 3px;
        color: #ffffff;
    }
    button.navbar-toggler.collapsed svg{fill:#fff}
.navbar .nav_link{padding: 10px;}
    .navbar-nav{
        background-color: var(--dark-to-main-color);
        box-shadow: var(--shadow-black-300);
    }
    .navbar .nav-item{
        margin: 0;
        padding: 5 15px;
    }
    .navbar .navbar-toggler,
    .navbar .navbar-nav{
        background-color: var(--main-color);
    }
    .home-img .circle{
        height: 320px;
        width: 320px;
    }
    .home-img .circle::before{
        left: 18px;
        top: 18px;
        transform-origin: 142px 142px;
    }
    .home-text h1{
        font-size: 40px;
    }
    .pricing-plan,
    .how-it-works-item,
    .app-download-item{
        margin-bottom: 30px;
    }
    .how-it-works-item.line-right::before{
        content: none;
    }
    .how-it-works-item{
        box-shadow: var(--shadow-black-100);
        border-radius: 10px;
    }
    .footer-col{
        margin-bottom: 30px;
    }
}

@media(max-width: 767px){
    .home-text{
        margin-bottom: 80px;
    }
    .fun-facts-img{
        margin-bottom: 30px;
    }
    .contact-form{
        margin-top: 20px;
    }
    .section-title h2{
        font-size: 35px;
    }
}
@media(max-width:575px){
    .home-text h1{
        font-size: 30px;
    }
    .home-text p{
        font-size: 16px;
    }
}

